<template>
  <div class="home">
    <div class="searchInput">
      <input type="text" v-model="value" placeholder="请输入快递单号">
      <div class="close" @click="value=''"></div>
      <div class="search" @click="searchFn"></div>
    </div>
    <div class="searchResult" style="display:none" ref="sr1">
      <h3>搜索结果</h3>
      <div class="content">
        <div class="header">
          <div class="time-title">
            <div class="time-text">时间</div>
            <div class="time-span" @click="sort=!sort">
              <div class="topT" :class="{active:sort}"></div>
              <div class="bottomT" :class="{active:!sort}"></div>
            </div>
          </div>
          <div class="addr-title">
            <div class="time-text">地点和跟踪进度</div>
          </div>
        </div>
        <div class="list" >
          <div class="listItem" v-for="(item,i) in showlist" :key="i">
            <div class="listTime">
              <div class="listTime-big">{{item.time.split(' ')[0]}}</div>
              <div class="listTime-small">{{item.time.split(' ')[1]}}</div>
            </div>
            <div class="listDetail">{{item.status}}</div>
            <div class="listDot" :class="{active:(sort&&i==0)||((!sort)&&i==(list.length-1))}"></div>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'

export default {
  name: 'Home',
  mounted(){
    console.log(this)
  },
  data(){
    return{
      value:'',
      list:[],
      sort:true
    }
  },
  computed:{
    showlist(){
      if(this.sort){
        return [...this.list]
      }else{
        return [...this.list].reverse()
      }
    }
  },
  methods:{
    async searchFn(){
      console.log('获取快递信息')
      let httpUrl = `https://wuliu.market.alicloudapi.com/kdi?no=${this.value}`
      let config = {
        headers:{
          'Authorization':'APPCODE 107ba3d02278433990bac80a743efb0a'
        }
      }
      let res = await axios.get(httpUrl,config)
      console.log(res)
      this.list = res.data.result.list
      this.$refs.sr1.style.display = 'block'
    }
  }
}
</script>

